.loader-main-div{
    height:100vh ;
    display: flex;
    justify-content: center;
    align-items: center;
}


.main-loader {
    color: #000000;
    position: relative;
    font-size: 11px;
    background: #000000;
    animation: escaleY 1s infinite ease-in-out;
    width: 1em;
    height: 4em;
    animation-delay: -0.16s;
  }
  .main-loader:before,
  .main-loader:after {
    content: '';
    position: absolute;
    top: 0;
    left: 2em;
    background: #000000;
    width: 1em;
    height: 5em;
    animation: escaleY 1s infinite ease-in-out;
  }
  .main-loader:before {
    left: -2em;
    animation-delay: -0.32s;
  }
  
  @keyframes escaleY {
    0%, 80%, 100% {
      box-shadow: 0 0;
      height: 4em;
    }
    40% {
      box-shadow: 0 -2em;
      height: 5em;
    }
  }